<template>
  <div>
    <main class="content container" v-if="productLoading"><PreUploader/></main>
    <main class="content container" v-else-if="!productData">Не удалось загрузить товар!</main>
    <main class="content container" v-if="productData">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{name: 'main'}">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{name: 'main'}">
              {{ category.title }}
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              {{ product.title }}
            </a>
          </li>
        </ul>
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img width="570" height="570" :src="product.image.file.url"
                 alt="product.title">
          </div>
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">
            {{ product.title }}
          </h2>
          <div class="item__form">
            <form class="form" action="#" method="POST" @submit.prevent="addToCard">
              <b class="item__price">
                {{ product.price | numberFormat }} ₽
              </b>

              <fieldset class="form__block">
                <legend class="form__legend">Цвет:</legend>

                <ColorBlock class="colors" :colors="productColors"
                            :current-color.sync="color"></ColorBlock>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Объемб в ГБ:</legend>

                <ul class="sizes sizes--primery">
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                      <span class="sizes__value">
                        32gb
                      </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                      <span class="sizes__value">
                        64gb
                      </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input class="sizes__radio sr-only" type="radio"
                             name="sizes-item" value="128" checked="">
                      <span class="sizes__value">
                        128gb
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <div class="item__row">
                <ItemCounter :count.sync="productAmount"/>
                <button class="button button--primery" type="submit" :disabled="productAddSending">
                  В корзину
                </button>
              </div>

              <div v-show="productAdded">Товар добавлен в корзину</div>
              <div v-show="productAddSending">Добавляем товар в корзину...</div>

            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a class="tabs__link tabs__link--current">
                Описание
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                Характеристики
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                Гарантия
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                Оплата и доставка
              </a>
            </li>
          </ul>

          <div class="item__content">
            <p>
              Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
              Синхронизация со смартфоном<br>
              Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
              Поддержка сторонних приложений<br>
            </p>

            <a href="#">
              Все характеристики
            </a>

            <h3>Что это?</h3>

            <p>
              Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать
              свои велотренировки, сделав их максимально эффективными. Wahoo ELEMNT BOLT GPS
              синхронизируется с датчиками по ANT+, объединяя полученную с них информацию.
              Данные отображаются на дисплее, а также сохраняются на смартфоне. При этом на
              мобильное устройство можно установить как фирменное приложение, так и различные
              приложения сторонних разработчиков. Велокомпьютер точно отслеживает
              местоположение, принимая сигнал с целого комплекса спутников. Эта информация
              позволяет смотреть уже преодоленные маршруты и планировать новые велопрогулки.
            </p>

            <h3>Дизайн</h3>

            <p>
              Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют
              всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты смартфона. Корпус гаджета
              выполнен из черного пластика. На обращенной к пользователю стороне расположен
              дисплей диагональю 56 мм. На дисплей выводятся координаты и скорость, а также
              полученная со смартфона и синхронизированных датчиков информация: интенсивность,
              скорость вращения педалей, пульс и т.д. (датчики не входят в комплект поставки).
              Корпус велокомпьютера имеет степень защиты от влаги IPX7. Это означает, что
              устройство не боится пыли, а также выдерживает кратковременное (до 30 минут)
              погружение в воду на глубину не более 1 метра.
            </p>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import ColorBlock from '@/components/ColorBlock.vue';
import ItemCounter from '@/components/ItemCounter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import PreUploader from '@/components/PreUploader.vue';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      productAmount: 1,
      color: 0,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      have404: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  name: 'ProductPage',
  components: { ColorBlock, ItemCounter, PreUploader },
  filters: {
    numberFormat,
  },
  computed: {
    productColors() {
      return this.productData.colors;
    },
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  created() {
    this.loadProduct();
  },
  watch: {
    color(value) {
      // eslint-disable-next-line prefer-destructuring
      this.color = value;
    },
    have404() {
      this.$router.replace({ name: 'notFound' });
    },
    // eslint-disable-next-line func-names
    '$route.params.id': function () {
      this.loadProduct();
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    gotoPage,
    addToCard() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;

      this.have404 = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
          // eslint-disable-next-line no-return-assign,arrow-parens
          .then(response => this.productData = response.data)
          // eslint-disable-next-line no-return-assign
          .then(() => this.color = this.productData.colors[0].id)
          // eslint-disable-next-line no-return-assign
          .catch((err) => {
            this.productLoadingFailed = true;
            if (err.response.data.error.code === 404) {
              this.have404 = true;
            }
          })
          // eslint-disable-next-line no-return-assign
          .then(() => { this.productLoading = false; this.have404 = false; });
      }, 0);
    },
  },
};
</script>

